
*{
	box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
}

.title {
	font-size: 1.7rem;
	margin: .5rem;

}

.logo{
	width: 60px;
	height: 60px;
	background-color: white;
	border-radius: 30px;

}

.logo-container{
	display: flex;
	align-items: center;
	padding: 5px;

}

.nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #333;
	color: white;
	padding: 15px;
    top: 0;
	width: 100%;
	position: sticky;
}

.navbar{
	margin: 0;
	padding: 0;
	display: flex;
}

.navbar li{
	list-style: none;
}

.navbar li a{
	text-decoration: none;
	color: white;
	padding: 1rem;
	display: block;
	font-size: larger;
}

.navbar li a:hover{
	background-color: #555;
}

.toggle{
	position: absolute;
    display: none;
    top: 1.5rem;
    right: 1rem;
    flex-direction: column;
	justify-content: space;
	float: right;
	width: 30px;
	height: 21px;
}

.toggle .bar{
	margin-top: 3px;
	height: 3px;
	width: 100%;
	background-color: white;
	border-radius: 10px;
}

.nav-dropdown {
  display: none !important;
  position: absolute;
  background-color: #333;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  flex-direction: column;
  padding: 0;
}


.nav-dropdown a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  text-align: left;
}


.nav-dropdown li:hover{
	background-color: #ddd;
}

.nav-dropdown a:hover {background-color: #ddd;}

.navbar li:hover .nav-dropdown {display: block !important;}


@media (max-width: 800px) {
	.toggle{
		display: flex;
	}

	.navbar{
		display: none;
		width: 100%;
		flex-direction: column;
	}

	.nav {
		flex-direction: column;
		aign-items: flex-start;
		display: block;
	}

	.nav-dropdown {
		min-width: 100%;
	}

	.navbar li{
		text-align: center;
	}

	.navbar li a{
		padding: .5rem 1rem;
	}

	.navbar.active{
		display: flex;
	}
}